.line-container hr {
    width:40%;
    size:2;
    margin-left: 0px;
}

.secondline-container hr {
    width:40%;
    size:2;
    margin-right: 0px;
}

.quote-container h1 {
    margin-top: 50px;
    margin-bottom: 50px;
    font-size: 50px;
    text-align: center;
}

@media (max-width: 768px) {
    .line-container hr {
        margin-top: 250px;
    }

    .quote-container h1 {
        font-size: 35px;
    }
}